import { WheelEvent, useEffect } from "react";

export const useScroll = () => {
  const scrollTo = (selector: string) => {
    const target = document.getElementById(selector);
    target?.scrollIntoView({
      block: "start",
      behavior: "smooth",
      inline: "start",
    });
  };

  const vhToPixels = (vh: number) => {
    return Math.round(window.innerHeight / (100 / vh));
  };

  const contentHeight = () => vhToPixels(100) - 100;

  const scrollContent = (ev: WheelEvent<HTMLDivElement>) => {
    const aboutPos = document
      ?.getElementById("about")
      ?.getBoundingClientRect().top;
    const experiencePos = document
      ?.getElementById("experience")
      ?.getBoundingClientRect().top;
    const projectsPos = document
      ?.getElementById("projects")
      ?.getBoundingClientRect().top;

    // console.log("positions", { aboutPos, experiencePos, projectsPos });

    switch (true) {
      case (aboutPos || 0) <= 0 && (aboutPos || 0) >= 0 - contentHeight():
        const about = document.getElementById("about-btn");
        about?.setAttribute("class", "active");
        break;
      case (experiencePos || 0) <= 0 &&
        (experiencePos || 0) >= 0 - contentHeight():
        const exp = document.getElementById("exp-btn");
        exp?.setAttribute("class", "active");
        break;
      case (projectsPos || 0) <= 0 && (projectsPos || 0) >= 0 - contentHeight():
        const proj = document.getElementById("proj-btn");
        proj?.setAttribute("class", "active");
    }
  };

  useEffect(() => {
    const toExp = document.getElementById("to-exp");
    const aboutBtn = document.getElementById("about-btn");
    const experienceBtn = document.getElementById("exp-btn");
    const projectsBtn = document.getElementById("proj-btn");

    aboutBtn?.addEventListener("click", () => {
      scrollTo("about");
      aboutBtn.setAttribute("class", "active");
    });

    toExp?.addEventListener("click", () => {
      scrollTo("experience");
      experienceBtn?.setAttribute("class", "active");
    });

    experienceBtn?.addEventListener("click", () => {
      scrollTo("experience");
      experienceBtn.setAttribute("class", "active");
    });

    projectsBtn?.addEventListener("click", () => {
      scrollTo("projects");
      projectsBtn.setAttribute("class", "active");
    });

    return () => {
      aboutBtn?.removeEventListener("click", () => {});
      toExp?.removeEventListener("click", () => {});
      experienceBtn?.removeEventListener("click", () => {});
      projectsBtn?.removeEventListener("click", () => {});
    };
  });

  return {
    scrollContent,
    scrollTo,
  };
};
