import React, { useCallback } from "react";
import { useNavigate } from "react-router-dom";
import moment from "moment";
import "../../style/list.css";
import { useAppSelector } from "../../app/hooks";
import { useLoadList } from "./hooks/useLoadList";

export default () => {
  const navigate = useNavigate();
  const al = useAppSelector((state) => state.article.list);
  const navigateHome = () => navigate("/");

  useLoadList();
  return (
    <div className="list-bg">
      <main className="list-wrapper">
        <h2>技術筆記</h2>
        <span className="back" onClick={navigateHome}>
          回首頁
        </span>
        <h3>私人筆記，用來紀錄學習的技術</h3>
        <div className="articles">
          {al.length > 0 ? (
            al.map((item) => {
              const click = () => {
                sessionStorage.setItem("blog_id", item.id);
                navigate("/article");
              };
              return (
                <div
                  className="article justify-between pointer"
                  onClick={click}
                >
                  <span>{item.title}</span>
                  <span>{moment(item.updated_at).format("YYYY/MM/DD")}</span>
                </div>
              );
            })
          ) : (
            <span>列表無文章</span>
          )}
        </div>
      </main>
    </div>
  );
};
