import { INIT } from "../../../app/init";
import { Article, List } from "../articleSlice";

export const useArticleApi = () => {
  const fetchList = async () => {
    const res = await fetch(`${INIT.url}/api/blog/hsin/list`);
    const json = await res.json();
    return json.list as List[];
  };

  const fetchDetail = async (id: string) => {
    const res = await fetch(`${INIT.url}/api/blog/${id}`);
    const json = await res.json();
    return json.blog as Article;
  };

  return { fetchList, fetchDetail };
};
