import React from "react";
import ReactQuill from "react-quill";
import moment from "moment";
import "react-quill/dist/quill.bubble.css";
import { useLoadDetail } from "./hooks/useLoadDetail";
import { useAppSelector } from "../../app/hooks";
import Back from "../../assets/images/arrow-left.svg";
import { useNavigate } from "react-router-dom";

export default () => {
  const navigate = useNavigate();
  const detail = useAppSelector((state) => state.article.current);

  useLoadDetail();

  if (!detail) return <div className="text-center m-5">載入中</div>;

  const content = JSON.parse(detail.ops);

  const handleClickBack = () => navigate("/list");

  return (
    <div style={{ width: "540px", margin: "0 auto" }}>
      <main className="position-relative container light-bg rounded">
        <img
          className="position-absolute"
          style={{ cursor: "pointer", top: "10px" }}
          src={Back}
          width="25"
          onClick={handleClickBack}
        />
        <h1 id="title" className="text-center">
          {detail.blog_title || "無文章標題"}
        </h1>
        <p>{`建立時間：${moment(detail.updated_at || "").format(
          "YYYY/MM/DD"
        )}`}</p>
        <ReactQuill theme="bubble" value={content} readOnly={true} />
      </main>
    </div>
  );
};
