import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useAppDispatch } from "../../../app/hooks";
import { useArticleApi } from "./useArticleApi";
import { updateArticle } from "../articleSlice";
import { aesDecoded } from "../../../utils";

export const useLoadDetail = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { fetchDetail } = useArticleApi();

  useEffect(() => {
    const id = sessionStorage.getItem("blog_id") as string;
    if (!id) {
      navigate("/list");
      return;
    }
    fetchDetail(aesDecoded(id)).then((article) =>
      dispatch(updateArticle(article))
    );
  }, []);
};
